@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Quicksand', sans-serif;
  line-height: 1.4;
}

a {
  text-decoration: none;
}

p {
  padding-bottom: 1em;
  text-align: justify;
}

h1 {
  font-weight: 700;
  font-size: 35px;
  text-align: center;
}

h2 {
  font-weight: 500;
  font-size: 30px;
  text-align: center;
}

h3 {
  font-weight: 500;
  font-size: 1.2em;
}

.App-Header {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    background-color: gray;
    padding: 8px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 9999;
}

.App-Header > a {
    font-size: 35px;
    font-weight: 300;
    color: white;
    grid-column-start: 1;
    justify-self: start;
    transition: none;
}

.App-Header > a:active {
    color: #f4a460;
}

.App-Bars {
    grid-column-start: 2;
    grid-row-start: 1;
    justify-self: end;
    align-self: center;
    font-size: 24px;
    color: white;
    cursor: pointer;
}

.App-Bars:active {
    color: #f4a460;
}

@media only screen and (min-width: 780px) {
    .App-Header > a {
        font-size: 45px;
        transition: color 200ms;
    }

    .App-Header > a:hover {
        color: #f4a460;
    }

    .App-Header > a:active {
        color: #515151;
    }

    .App-Bars {
        display: none;
    }
}

.App-NavLinkItem {
    display: block;
}

.App-NavLinkItem a {
    font-family: 'Quicksand', sans-serif;
    font-size: 24px;
    font-weight: 400;
    color: white;
    padding: 8px;
    transition: none;
}

.App-NavLinkItem a:active {
    color: #f4a460;
}

@media only screen and (min-width: 780px) {
    .App-NavLinkItem {
        display: inline;
    }
    .App-NavLinkItem a {
        transition: color 200ms;
    }
    .App-NavLinkItem a:hover {
        color: #f4a460;
    }
    .App-NavLinkItem a:active {
        color: #515151;
    }
}

.App-NavLinks {
    grid-row-start: 2;
    grid-column: 1 / span 2;
}

.App-NavLinks ul {
    list-style: none;
    display: none;
    text-align: center;
}

.visible {
    display: block !important;
}

@media only screen and (min-width: 780px) {
    .App-NavLinks {
        grid-column: 2 / span 1;
        grid-row-start: 1;
        align-self: center;
        justify-self: end;
    }

    .App-NavLinks ul {
        display: block;
    }
}

.App-Main {
    padding: 8px 16px;
}

.Main-Container {
    margin: auto;
    max-width: 1200px;
}

.dark {
    background-color: #333333;
    color: #eee;
}

.light {
    background-color: #eee;
    color: #333333;
}

.App-About {
    display: grid;
    grid-template-rows: auto auto auto auto auto;
    grid-template-columns: auto;
}

.App-About h1 {
    grid-row-start: 1;
    grid-column-start: 1;
}

.about-image {
    grid-row-start: 2;
    grid-column-start: 1;
    width: 300px;
    height: 300px;
    margin: 10px auto 20px;
    display: block;
    border: ridge 10px gray;
    border-radius: 20% 10%;
}

.about-text {
    grid-column-start: 1;
    grid-row-start: 3;
}

.App-About h2 {
    grid-column: 1 / span 2;
    grid-row-start: 4;
}

.about-skills {
    grid-column: 1 / span 2;
    grid-row-start: 5;
}

@media only screen and (min-width: 780px) {
    .App-About {
        grid-template-rows: auto auto auto auto;
        grid-template-columns: auto auto;
    }

    .App-About h1 {
        grid-row-start: 1;
        grid-column: 1 / span 2;
    }

    .about-image {
        grid-row-start: 2;
        grid-column-start: 1;
        margin-right: 30px;
    }

    .about-text {
        grid-row-start: 2;
        grid-column-start: 2;
    }

    .App-About h2 {
        grid-column: 1 / span 2;
        grid-row-start: 4;
    }

    .about-skills {
        grid-column: 1 / span 2;
        grid-row-start: 5;
    }
}

.Project {
    width: 280px;
    height: 280px;
    background-color: #ffffff;
    margin: 10px;
    position: relative;
}

.info {
    position: relative;
    z-index: 10;
}

.Project img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    object-fit: cover;
}

.Project-Content {
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: 100%;
    z-index: 10;
    color: white;
    padding: 10px;
    background-color: rgba(143, 143, 143, 0.7);
}

.Scroll {
    overflow: auto;
}

.Project-Content-Para-Hidden {
    display: none;
}

.project-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.Contact-Container {
    text-align: center;
}

.Contact-Icon {
    display: inline-block;
    margin: 10px;
    font-size: 80px;
    line-height: 1em;
    height: 1em;
    transition: color 200ms;
}

.Contact-Icon:hover {
    color: #f4a460;
}

.Contact-Icon:active {
    color: #515151;
}

.App-Footer {
    background-color: #f4a460;
}

.App-Footer p {
    padding-top: 1em;
    text-align: center;
}

